import { Trans } from '@lingui/macro'
import { BlurOff, BlurOn } from '@material-ui/icons'
import React from 'react'
import styled from 'styled-components'

import { FormInputStyled } from '../../../atoms/FormInputStyled'
import { PasswordInput } from '../../../atoms/PasswordInput'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'

interface IProps {
  onSubmitChange(password: string): void
  buttonLabel: string
}

const StyledPasswordChangeForm = styled.div`
  .password {
    display: inline-block;
    margin: 0.5rem;

    button {
      display: inline-block;
      width: 3rem;
    }
  }

  .submit {
    width: 14rem;
    margin: 0.5rem;
  }
`

const ErrorContainer = styled.div`
  background-color: ${Colors.red};
  opacity: 0.5;
  padding: 1rem;
  width: 31rem;
`

export const PasswordChangeForm: React.FC<IProps> = ({
  onSubmitChange,
  buttonLabel,
}) => (
  <PasswordInput>
    {({
      getInputProps,
      valid,
      errors,
      touched,
      isVisible,
      toggleShowPassword,
      getPassword,
    }) => (
      <StyledPasswordChangeForm>
        <div className="password">
          <FormInputStyled
            {...getInputProps()}
            name="password"
            placeholder={i18n.t`new password`}
            invalid={touched && !valid}
          />
          <TouchButton
            color={Colors.greyMiddle}
            onClick={toggleShowPassword}
            icon={isVisible ? BlurOff : BlurOn}
            iconSize={2}
          />
        </div>

        <div className="submit">
          <TouchButton
            color={Colors.green}
            disabled={!valid}
            onClick={e => {
              e.preventDefault()
              onSubmitChange(getPassword())
            }}
          >
            {buttonLabel}
          </TouchButton>
        </div>

        {touched && !valid && (
          <ErrorContainer>
            <Trans>Password is invalid due to following errors:</Trans>
            <ul>
              {errors.map(e => (
                <li key={e.key}>{e.message}</li>
              ))}
            </ul>
          </ErrorContainer>
        )}
      </StyledPasswordChangeForm>
    )}
  </PasswordInput>
)
