import { Sentry, useSentryScope } from '@speedlo/sentry'
import React from 'react'
import { Redirect, RouteComponentProps } from 'react-router-dom'

import { useRoot } from '../../../atoms/RootProvider'
import { useMPasswordChange } from '../../../graph/generated'
import { logAuth } from '../../../helpers/logger'
import { useSnackBar } from '../../../hooks/useSnackBar'
import { i18n } from '../../../i18n'
import {
  IPasswordChangeProps,
  PasswordChangePage,
} from '../pages/PasswordChangePage'

export const PasswordChangeRoute: React.FC<
  RouteComponentProps<{ token: string }>
> = ({ history, location, match }) => {
  const { auth } = useRoot()
  const { enqueueError, enqueueSuccess } = useSnackBar()
  const scope = useSentryScope()

  const { token: tempToken } = match.params
  const decodedTempToken = window.atob(tempToken)

  scope.setExtras({ tempToken, decodedTempToken })

  const [changePassword] = useMPasswordChange({
    fetchPolicy: 'no-cache',
    context: {
      overrideWithToken: decodedTempToken,
    },
  })

  if (!tempToken) {
    logAuth('no token has been passed to change password')
    enqueueError(
      i18n.t`Request for password update is invalid. Please try again.`,
    )
    return <Redirect to="/" />
  }

  const onPasswordChange = async (password: string) => {
    const result = await changePassword({
      password,
    })
    if (result) {
      const {
        change: { userId, newToken },
      } = result
      auth.invalidate()
      auth.authenticate(newToken, userId)
      enqueueSuccess(i18n.t`Password has been updated.`)
    } else {
      Sentry.captureMessage('Password change failed')
      enqueueError(
        i18n.t`Request for password update is invalid. Please try again.`,
      )
    }
    history.replace('/')
  }

  const props: IPasswordChangeProps = {
    onPasswordChange,
    pageTitle: '',
    buttonLabel: '',
  }

  if (location.pathname.includes('register')) {
    props.pageTitle = i18n.t`Set up password`
    props.buttonLabel = i18n.t`Create password`
  } else if (location.pathname.includes('recovery')) {
    props.pageTitle = i18n.t`Password recovery`
    props.buttonLabel = i18n.t`Update password`
  } else {
    return <Redirect to="/" />
  }
  return <PasswordChangePage {...props} />
}

export default PasswordChangeRoute
